import React, {useEffect, useState} from 'react'
import {NavLink, useLocation} from "react-router-dom";

import {GiHamburgerMenu} from 'react-icons/gi'
import Path from "../router/Path";

function AsideNav() {
    const location = useLocation()
    const [visible, setVisible] = useState(false)

    useEffect(() => {
        setVisible(false)
    }, [location])

    const toggleHandler = (visible: boolean) => {
        setVisible(visible)
    }

    return (
        <div>
            {
                !visible && (
                    <div className="fixed z-50 left-8 top-24 cursor-pointer" onClick={() => toggleHandler(true)}>
                        <GiHamburgerMenu className="text-menu text-3xl"/>
                    </div>
                )
            }
            <div
                className={`fixed z-50 left-0 top-16 h-main px-8 pt-12 bg-menu transition transform ${!visible && '-translate-x-full'}`}
                style={{width: '300px'}}
            >
                <div className="absolute right-4 top-4 cursor-pointer" onClick={() => toggleHandler(false)}>
                    <i className="el-icon-close text-white"/>
                </div>
                <NavLink className="hover:text-black-100" to={Path.AmazonTop100DashBoard()}>
                    <p className="text-2xl text-white">Dashboard</p>
                </NavLink>
                <div className="mt-8">
                    <h3 className="text-2xl text-white border-b-2 border-solid border-white px-1">Market Analysis</h3>
                    <div className="pl-2">
                        <NavLink to={Path.AmazonTop100PriceDistribution()}>
                            <p className="mt-4 text-xl text-white">Price Distribution</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100Inventory()}>
                            <p className="mt-4 text-xl text-white">Inventory</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100NewReviews()}>
                            <p className="mt-4 text-xl text-white">New Reviews</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100BrandRanking()}>
                            <p className="mt-4 text-xl text-white">Brand Ranking</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100ProductsRanking()}>
                            <p className="mt-4 text-xl text-white">Products Ranking</p>
                        </NavLink>
                    </div>
                </div>
                <div className="mt-8">
                    <h3 className="text-2xl text-white border-b-2 border-solid border-white px-1">SEO Analysis</h3>
                    <div className="pl-2">
                        <NavLink to={Path.AmazonTop100AmazonsChoice()}>
                            <p className="mt-4 text-xl text-white">Amazon's Choice</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100Sponsored()}>
                            <p className="mt-4 text-xl text-white">Sponsored</p>
                        </NavLink>
                        <NavLink to={Path.AmazonTop100KeywordAnalysis()}>
                            <p className="mt-4 text-xl text-white">Keyword Analysis</p>
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AsideNav
