import dayjs from "dayjs";
import {FormGroup} from "../types/filter";
import {INPUT_TYPE} from "../components/Collapse";

export function formatDate(value: ElementReactLibs.dateType[]): [string, string] {
    return [dayjs(value[0]).format('YYYY-MM-DD'), dayjs(value[1]).format('YYYY-MM-DD')]
}

export function initCategoryOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'Webcam', value: 'webcam'},
        {label: 'Doc cam', value: 'document camera'},
        {label: 'Projector', value: 'video projector'},
    ]
}

export function initRegionOptions(): ElementReact.SelectOptionProps[] {
    return [
        {label: 'US', value: 'US'},
        {label: 'UK', value: 'UK'},
    ]
}

export function initDate(): ElementReactLibs.dateType[] {
    const startDate = dayjs().subtract(2, 'week').startOf('week').toDate()
    const endDate = dayjs().subtract(1, 'week').startOf('week').toDate()
    return [startDate, endDate];
}

