import {Carousel} from 'element-react';
import React, {useRef} from 'react'
import {HiDownload} from "react-icons/hi";
import {downloadPng} from "../../utils/download";
import ProgressChart from "../../components/ProgressChart";
import AverageChart from "../../components/AverageChart";
import SeoBarLineChart from "../../components/SeoBarLineChart";
import {Sponsored, SponsoredAvg, SponsoredRate} from "../../types/seo";
import {BarLine, BarLineChartData} from "../../types/market";

interface RankBoxProps {
    title: string;
    rank: number;
    color?: string
}

function RankBox(props: RankBoxProps) {
    const {title, rank, color = '#ACACAC'} = props;
    return (
        <div className="inline-flex flex-col justify-center items-center border-4 border-solid rounded py-4 px-8 mx-4"
             style={{color, borderColor: color, width: '300px'}}>
            <h2 className="text-5xl pb-2">{title}</h2>
            <p className="text-2xl pb-2">Average ranking</p>
            <p className="text-8xl pb-2">{rank}</p>
        </div>
    )
}

interface RankComparisonProps {
    height?: string;
    title?: string;
    data: Sponsored | null;
    rate: SponsoredRate | null;
    avg: SponsoredAvg | null;

    onClick?(value: number): void;
}

function formatBarLineData(data: SponsoredRate | null): BarLineChartData | null {
    if (!data) return null;
    const axis = data.info.x_axis;
    const series: BarLine[] = [
        {
            type: 'bar',
            name: 'Sponsored',
            // @ts-ignore
            data: Object.keys(data.sponsored).map((key: string) => data.sponsored[key]),
            yAxisIndex: 0,
            stack: 'one',
        },
        {
            type: 'bar',
            name: 'Not Sponsored',
            // @ts-ignore
            data: Object.keys(data.no_sponsored).map((key: string) => data.no_sponsored[key]),
            yAxisIndex: 0,
            stack: 'one',
        },
        {
            type: 'line',
            name: 'Sponsored Rate',
            // @ts-ignore
            data: Object.keys(data.sponsored).map((key: string) => Math.round(data.sponsored[key] * 100 / (data.sponsored[key] + data.no_sponsored[key]))),
            yAxisIndex: 1,
        }
    ]
    return {axis, series}
}

function formatAverageData(data: SponsoredAvg | null) {
    if (!data) return null;
    return data
}

function RankComparison(props: RankComparisonProps) {
    const shotWrapperRef = useRef<HTMLDivElement | null>(null)

    const {title = 'Title', height = '300px', data = null, rate = null, avg = null, onClick} = props;

    const onDownload = async () => {
        if (!shotWrapperRef.current) return;
        await downloadPng(shotWrapperRef.current, title)
    }

    return (
        <div ref={shotWrapperRef} className="relative" style={{height}}>
            <Carousel className="mt-4" indicatorPosition="none" autoplay={false}>
                <Carousel.Item>
                    <h1 className="text-2xl">Sponsored Average Rank Comparison</h1>
                    <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
                    <div className="flex justify-center mt-4" style={{height: '250px'}}>
                        {
                            data && <RankBox title="Sponsored" rank={data.sponsored_avg} color="#FB8C3B"/>
                        }
                        {
                            data && <RankBox title="Others" rank={data.unsponsored_avg}/>
                        }
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <SeoBarLineChart
                        height="300px"
                        width="728px"
                        title="Sponsored Rate"
                        names={['Amount', 'Sponsored Rate']}
                        data={formatBarLineData(rate)}
                        yAxisLabel={'Ranking'}
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <AverageChart
                        height="300px"
                        width="728px"
                        data={formatAverageData(avg)}
                    />
                </Carousel.Item>
            </Carousel>
            <div className="mt-8">
                {
                    data &&
                    <ProgressChart
                      title="Top Sponsored Ratio"
                      colors={["#FB8C3B", "#ACACAC"]}
                      data={[{source: 'Sponsored', count: data.sponsored_rate}, {
                          source: 'Others',
                          count: data.unsponsored_rate
                      }]}
                      onClick={onClick}
                    />
                }
            </div>
        </div>
    )
}

export default RankComparison
