import React, {useEffect, useRef} from 'react'
import * as echarts from "echarts/core";
import {HiDownload} from "react-icons/hi";
import {downloadPng} from "../../../utils/download";
import {LineChart, LineSeriesOption} from "echarts/charts";
import {
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    DataZoomComponent,
    DataZoomComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";

type ECOption = echarts.ComposeOption<LineSeriesOption | GridComponentOption | TooltipComponentOption | DataZoomComponentOption>;

echarts.use([LineChart, GridComponent, TooltipComponent, DataZoomComponent, CanvasRenderer])

function Price(props: {axis: string[]; data: number[]}) {
    const {axis, data} = props;
    const chartRef = useRef<HTMLDivElement | null>(null);
    // const [selectValue, setSelectValue] = useState(1); // Unit: Month

    const option: ECOption = {
        grid: {
            top: '10%',
            right: '5%',
            bottom: '20%',
            left: '10%',
        },
        dataZoom: [
            {
                show: true,
                realtime: true,
            },
            {
                type: 'inside',
                show: true,
                realtime: true,
            }
        ],
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: axis
            }
        ],
        yAxis: [
            {
                type: 'value',
            }
        ],
        series: [
            {
                type: 'line',
                name: 'Price',
                data: data
            },
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    // const rangeChangeHandler = (value: number) => {
    //     setSelectValue(value)
    // }

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, 'title')
    }

    return (
        <div className="relative border-2 rounded mx-8 mt-10" style={{height: '400px'}}>
            <HiDownload className="absolute right-2 top-2 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
            <div className="flex items-center mt-2 px-2">
                <h1 className="text-xl mr-4">Price in Past</h1>
                {/*<div className="flex">*/}
                    {/*<Button type={selectValue === 1 ? 'primary' : undefined}*/}
                            {/*size="mini"*/}
                            {/*onClick={() => rangeChangeHandler(1)}*/}
                    {/*>1 Month</Button>*/}
                    {/*<Button type={selectValue === 3 ? 'primary' : undefined}*/}
                            {/*size="mini"*/}
                            {/*onClick={() => rangeChangeHandler(3)}*/}
                    {/*>3 Months</Button>*/}
                    {/*<Button type={selectValue === 6 ? 'primary' : undefined}*/}
                            {/*size="mini"*/}
                            {/*onClick={() => rangeChangeHandler(6)}*/}
                    {/*>6 Months</Button>*/}
                    {/*<Button type={selectValue === 12 ? 'primary' : undefined}*/}
                            {/*size="mini"*/}
                            {/*onClick={() => rangeChangeHandler(12)}*/}
                    {/*>1 Year</Button>*/}
                {/*</div>*/}
            </div>
            <div ref={chartRef} style={{height: '350px', width: '570px'}}/>
        </div>
    )
}

export default Price
