import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {BarChart, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    data: [string[], {data: string, value: number}[]]
    dataItemName: string;
    onClick?(value: string): void;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption>;

echarts.use([BarChart, TitleComponent, GridComponent, TooltipComponent, CanvasRenderer]);

function HorizontalBarChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, data = [[],[]], dataItemName = '', onClick} = props;

    const [yAxisData, seriesData] = data;

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: titleFontSize
            }
        },
        grid: {
            top: 50,
            right: 50,
            bottom: 40,
            left: 100,
        },
        tooltip: {
            trigger: 'axis',
            position: 'top',
            confine: true,
            formatter: "<div style='display:block; word-break: break-all;word-wrap: break-word; white-space:pre-wrap; width: 400px;'>" + "{b} " + "</div>" + " <br/>{a}:{c}"
        },
        xAxis: [
            {
                type: 'value',
            }
        ],
        yAxis: [
            {
                type: 'category',
                data: yAxisData,
                inverse: true,
                axisLabel: {
                    width: 100,
                    fontFamily: 'monospace',
                    formatter(value: any) {
                        const TOTAL = 11;
                        let res = value;
                        if(res.length > 9) {
                            return res.substring(0, 8) + "...";
                        }
                        return res + Array(TOTAL - res.length).fill(' ').join('');
                    }
                }
            }
        ],
        series: [
            {
                type: 'bar',
                name: dataItemName,
                yAxisIndex: 0,
                data: seriesData.map((it: {data: string, value: number}) => it.value)
            },
        ]
    };

    useEffect(() => {
        if (!chartRef.current) return;

        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option);
        if (onClick) {
            chart.on('click', (params) => {
                if (onClick) {
                    onClick(seriesData[params.dataIndex].data)
                }
            })
        }
    });

    const onDownload = async () => {
        if (!chartRef.current) return;
        await downloadPng(chartRef.current, title)
    };

    return (
        <div className="relative">
            <div ref={chartRef} style={{width, height}}/>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
        </div>
    )
}

export default HorizontalBarChart
