import React, {useEffect, useState} from 'react'
import {Button, Card, Loading, Select} from "element-react";

import BarLineChart from "../components/BarLineChart";
import DoughnutChart from "../components/DoughnutChart";
import PercentageDoughnutChart from "../components/PercentageDoughnutChart";
import HorizontalBarChart from "../components/HorizontalBarChart";
import InfoCard from '../components/InfoCard'
import VerticalProgressBarChart from "../components/VerticalProgressBarChart";
import ProductsDownloadDialog from "../components/ProductsDownloadDialog";
import {initCategoryOptions, initRegionOptions} from "../utils/common";
import {
    getAmazonTop100DashBoardTime, getFtiRankingChart,
    getNewCommentBarChart,
    getPopularPriceRange,
    getPopularResolutions,
    getPriceDistributionBarLineChart,
    getTopBrandPieChart,
    getTopProductBarChart
} from "../apis/market";
import {
    BarLine,
    BarLineChartData,
    NewCommentBarChart,
    NewCommentBarItem,
    PopularPriceRangeResponse,
    PopularResolutionResponse,
    PriceDistributionBarLineChart,
    ProductBarChart,
    ProductBarItem,
    SearchParams,
    TopBrandPieItem
} from "../types/market";
import {getAmazonsChoice, getSponsored} from "../apis/seo";
import {AmazonsChoice, Sponsored} from "../types/seo";
import {PieChartItem} from "../types/chart";
import ProductCards from "../components/ProductCards";
import {FtiRankingChartResponse, FtiRankingItemResponse, ProductsParams, ProductsResponse} from "../types/product";
import {getProducts} from "../apis/product";
import {formatBarLineData} from "../utils/chart";

function formatData(data: NewCommentBarChart): [string[], { data: string, value: number }[]] {
    const formatContent = data.items.content.sort((a: NewCommentBarItem, b: NewCommentBarItem) => b.review - a.review);
    const yAxisData = formatContent.map((it: NewCommentBarItem) => it.name);
    const seriesData = formatContent.map((it: NewCommentBarItem) => ({data: it.asin, value: it.review}));
    return [yAxisData, seriesData]
}

function formatTopBrandData(data: ProductBarChart): [string[], { name: string, data: string, value: number }[], number[]] {
    const formatContent = data.items.content.sort((a: ProductBarItem, b: ProductBarItem) => b.count - a.count);
    const yAxisData = formatContent.map((it: ProductBarItem) => it.name);
    const seriesData = formatContent.map((it: ProductBarItem) => ({name: it.name, data: it.ASIN, value: it.count}));
    const ranks = formatContent.map((it: ProductBarItem) => it.avg_rank);
    return [yAxisData, seriesData, ranks]
}

function formatFtiRankingChartData(data: FtiRankingChartResponse | null): BarLineChartData | null {
    if (!data) return null;

    const axis = data.data
        .map((it: FtiRankingItemResponse) => it.mission)
        .reduce((ary: string[], mission: string) => {
            if (!ary.includes(mission)) {
                ary.push(mission)
            }
            return ary
        },[]);
    const models = data.data
        .map((it: FtiRankingItemResponse) => it.model)
        .reduce((ary: string[], mode: string) => {
            if (!ary.includes(mode)) {
                ary.push(mode)
            }
            return ary
        },[]);
    ;
    const series: BarLine[] = models.map((model: string) => {
        const target = data.data
            .filter((it: FtiRankingItemResponse) => it.model === model)
            .map((it: FtiRankingItemResponse) => it.ranking);
        return {
            type: 'line',
            name: model,
            data:  target,
            yAxisIndex: 0,
        }
    });
    return {axis, series}
}

function AmazonTop100DashBoard() {
    const [category, setCategory] = useState('webcam');
    const [region, setRegion] = useState('US');
    const [sorting, setSorting] = useState('ranking');

    const [productsPage, setProductsPage] = useState(1);

    const [time, setTime] = useState('');
    const [distribution, setDistribution] = useState<PriceDistributionBarLineChart | null>(null);
    const [amazonsChoice, setAmazonsChoice] = useState<AmazonsChoice | null>(null);
    const [popularResolution, setPopularResolution] = useState<PopularResolutionResponse | null>(null);
    const [popularPriceRange, setPopularPriceRange] = useState<PopularPriceRangeResponse | null>(null);
    const [sponsored, setSponsored] = useState<Sponsored | null>(null);
    const [topBrandData, setTopBrandData] = useState<PieChartItem[]>([]);
    const [brandProducts, setBrandProducts] = useState<ProductBarChart | null>(null);
    const [newComment, setNewComment] = useState<NewCommentBarChart | null>(null);
    const [products, setProducts] = useState<ProductsResponse | null>(null);
    const [amazonsBrandValue, setAmazonsBranValue] = useState('');
    const [sponsoredBrandValue, setSponsoredBrandValue] = useState('');
    const [ftiRanking, setFtiRanking] = useState<FtiRankingChartResponse | null>(null);

    const [visible, setVisible] = useState(false);
    const [chartLoading, setChartLoading] = useState(false);

    const categoryOptions = initCategoryOptions();
    const regionOptions = initRegionOptions();
    const sortingOptions: ElementReact.SelectOptionProps[] = [
        {label: 'Recent Ranking', value: 'ranking'},
        {label: 'Recent Price', value: 'price'}
    ];

    useEffect(() => {
        const fetchData = async () => {
            const {data} = await getAmazonTop100DashBoardTime();
            setTime(data.dashboard_time)
        };
        fetchData()
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            setChartLoading(true);
            const params: SearchParams = {
                category,
                region,
            };
            try {
                setFtiRanking((await getFtiRankingChart(params)).data);
                setDistribution((await getPriceDistributionBarLineChart(params)).data);
                setAmazonsChoice((await getAmazonsChoice(params)).data);
                setSponsored((await getSponsored(params)).data);
                setTopBrandData((await getTopBrandPieChart(params)).data.items
                    .map((it: TopBrandPieItem): PieChartItem => ({name: it.brand, value: it.count})));
                setBrandProducts((await getTopProductBarChart(params)).data);
                setNewComment((await getNewCommentBarChart(params)).data);
                setPopularResolution((await getPopularResolutions(params)).data);
                setPopularPriceRange((await getPopularPriceRange(params)).data)

            } finally {
                setChartLoading(false)
            }
        };
        fetchData()
    }, [category, region]);

    useEffect(() => {
        const fetchData = async () => {
            const params: ProductsParams = {category, region, page: productsPage, sorting};
            const {data} = await getProducts(params);
            setProducts(data)
        };
        fetchData()
    }, [category, region, sorting, productsPage]);

    const handleClickTopBrandChart = async (brand: string) => {
        const params: SearchParams = {
            category,
            region,
            brand
        };
        if (brand === 'others') {
            const {data} = await getTopBrandPieChart({...params, dashboard: 1});
            // @ts-ignore
            params.others = data.brands.reduce((str: string, item: string, index: number) => {
                if (index) {
                    return `${str},${item}`
                } else {
                    return item
                }
            }, '');
        }
        setAmazonsBranValue(brand);
        setSponsoredBrandValue(brand);
        setAmazonsChoice((await getAmazonsChoice(params)).data);
        setSponsored((await getSponsored(params)).data)
    };

    const handleClearTopBrandChart = async (type: string) => {
        const params: SearchParams = {
            category,
            region,
        };
        if (type === 'sponsored') {
            setSponsoredBrandValue('');
            setSponsored((await getSponsored(params)).data)
        }

        if (type === 'amazons') {
            setAmazonsBranValue('');
            setAmazonsChoice((await getAmazonsChoice(params)).data);
        }
    };

    const handleDownloadProducts = async (asin: string[]) => {
        const params: ProductsParams = {
            category,
            region,
            dl: 1,
            asin: asin.join(',')
        };
        const {request} = await getProducts(params);
        window.open(request.responseURL, '__blank')
    };

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-end mb-4">
                <Select className="mr-4" value={category} placeholder="Select" onChange={value => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select" onChange={value => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>

                <p className="text-xl text-context">Last Update: {time}</p>
            </div>

            {/* Dash Board */}
            {
                chartLoading
                    ? (
                        <div className="bg-background flex justify-center items-center" style={{minHeight: '300px'}}>
                            <Loading/>
                        </div>
                    )
                    : (
                        <div>
                            <div className="flex">
                                <div className="w-7/12 pr-2">
                                    <Card className="mb-4">
                                        <BarLineChart
                                            title="Price Distribution"
                                            titleFontSize={18}
                                            height="400px"
                                            names={['Quantity', 'Value (M in USD)']}
                                            totals={[
                                                {name: 'Review Quantity'},
                                                {name: 'Product Model'},
                                                {name: 'Revenue(million USD)', fixed: true}
                                            ]}
                                            data={formatBarLineData(distribution, category)}
                                        />
                                    </Card>
                                    <div className="flex mb-4">
                                        <div className="w-1/2 pr-2">
                                            {
                                                popularResolution &&
                                                <InfoCard title="Most Popular Resolution" context={popularResolution.tag}
                                                          reviewsContext={`${popularResolution.review_number}`}/>
                                            }
                                        </div>
                                        <div className="w-1/2 pl-2">
                                            {
                                                popularPriceRange &&
                                                <InfoCard title="Most Popular Price Range" context={popularPriceRange.price}
                                                          reviewsContext={`${popularPriceRange.review}`}/>
                                            }
                                        </div>
                                    </div>
                                    <Card>
                                        {
                                            newComment &&
                                            <HorizontalBarChart
                                                title="Top Product - New Reviews"
                                                titleFontSize={18}
                                                height="400px"
                                                data={formatData(newComment)}
                                                dataItemName='Reviews'
                                            />
                                        }
                                    </Card>
                                </div>
                                <div className="w-5/12 pl-2">
                                    <Card className="mb-4">
                                        <DoughnutChart
                                            title="Top Brand"
                                            titleFontSize={18}
                                            subtitle="Product Models in Top 100"
                                            subtitleFontSize={16}
                                            height="300px"
                                            data={topBrandData}
                                            hasLegend
                                            onClick={handleClickTopBrandChart}
                                        />
                                    </Card>
                                    <div className="flex mb-4">
                                        <div className="w-1/2 pr-2">
                                            <Card>
                                                <PercentageDoughnutChart
                                                    height="200px"
                                                    title="Sponsored Rate"
                                                    titleFontSize={18}
                                                    name="Sponsored"
                                                    brand={sponsoredBrandValue}
                                                    color="#fb8c3b"
                                                    percentage={sponsored ? Math.round(sponsored.sponsored_rate * 100) : 0}
                                                    onClear={() => handleClearTopBrandChart('sponsored')}
                                                />
                                            </Card>
                                        </div>
                                        <div className="w-1/2 pl-2">
                                            <Card>
                                                <PercentageDoughnutChart
                                                    height="200px"
                                                    title="Amazon's Choice"
                                                    titleFontSize={18}
                                                    name="Amazon’s Choice"
                                                    brand={amazonsBrandValue}
                                                    color="#2e78b4"
                                                    percentage={amazonsChoice ? Math.round(amazonsChoice.amazons_choice_rate * 100) : 0}
                                                    onClear={() => handleClearTopBrandChart('amazons')}
                                                />
                                            </Card>
                                        </div>
                                    </div>
                                    <Card>
                                        {
                                            brandProducts &&
                                            <VerticalProgressBarChart
                                              title="Top Product - No. of Times in Top 30"
                                              titleFontSize={18}
                                              height="400px"
                                              data={formatTopBrandData(brandProducts)}
                                              padding="0 20px 0 50px"
                                            />
                                        }
                                    </Card>
                                </div>
                            </div>
                            <div className="w-7/12 pr-2 mt-4">
                                <Card>
                                    <BarLineChart
                                        title="FTI Ranking"
                                        titleFontSize={18}
                                        height="400px"
                                        names={['']}
                                        inverse
                                        data={formatFtiRankingChartData(ftiRanking)}
                                    />
                                </Card>
                            </div>
                        </div>
                    )
            }


            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex justify-between items-end">
                    <div className="flex">
                        <p className="text-2xl mr-2">Top 100</p>
                        <Button type="primary" onClick={() => setVisible(true)}>Download</Button>
                    </div>
                    <Select value={sorting} placeholder="Select" onChange={(value: string) => setSorting(value)}>
                        {
                            sortingOptions.map(it => {
                                return <Select.Option key={it.value} label={it.label} value={it.value}/>
                            })
                        }
                    </Select>
                </div>
                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>

            <ProductsDownloadDialog
                visible={visible}
                setVisible={setVisible}
                params={{
                    category,
                    region
                }}
                onDownload={handleDownloadProducts}
            />
        </div>
    )
}

export default AmazonTop100DashBoard
