import {BarLine, BarLineChartData, PriceDistributionBarLineChart, PriceDistributionItem} from "../types/market";

export const takeIntervalAxisSetting = (list: number[], isPercentage?: boolean): { max: number; min: number; interval: number } => {
    if (isPercentage) {
        return {
            min: 0,
            max: 100,
            interval: 20
        }
    }
    const SPLIT_NUMBER = 5;
    const _min = list.reduce((m, num) => m > num ? num : m, 0);
    const _max = list.reduce((m, num) => m < num ? num : m, 0);
    const min = _min > 5 ? _min - SPLIT_NUMBER + (_min % SPLIT_NUMBER) : 0;
    const max = _max > 5 ? _max + SPLIT_NUMBER - (_max % SPLIT_NUMBER) : 5;
    const interval = (max - min) / SPLIT_NUMBER;
    return {
        min,
        max,
        interval
    }
}

export function formatBarLineAxis(axis: string[], category?: string): string[] {
    if (!category) return axis;
    const copy = [...axis];
    if (category === 'webcam') {
        copy[0] = '10~20';
    }
    if (category === 'video projector') {
        copy[0] = '17~30';
    }
    if (category === 'document camera') {
        copy.splice(0, 1);
        copy[0] = '21~40';
    }
    return copy
}

export function filterFirstWhenDocumentCamera(data: number[], category?: string) {
    if (category === 'document camera') {
        const copy = [...data]
        copy.splice(0, 1)
        return copy
    }
    return data
}


export function formatBarLineData(data: PriceDistributionBarLineChart | null, category?: string): BarLineChartData | null {
    if (!data) return null;

    const axis = formatBarLineAxis(data.info.x_axis, category)
    const series: BarLine[] = [
        {
            type: 'bar',
            name: 'Review Quantity',
            data: filterFirstWhenDocumentCamera(
                data.data.map((it: PriceDistributionItem) => it.sold),
                category
            ),
            yAxisIndex: 0,
        },
        {
            type: 'bar',
            name: 'Product Model',
            data: filterFirstWhenDocumentCamera(
                data.data.map((it: PriceDistributionItem) => it.count),
                category
            ),
            yAxisIndex: 0,
        },
        {
            type: 'line',
            name: 'Revenue(million USD)',
            data: filterFirstWhenDocumentCamera(
                data.data.map((it: PriceDistributionItem) => it.revenue),
                category
            ),
            yAxisIndex: 1,
        }
    ];
    return {axis, series}
}

export const takeChartTypeData = (typeIndex: number, data: BarLineChartData | null): BarLine[] => {
    return data ? data.series.filter((it: BarLine) => it.yAxisIndex === typeIndex) : []
};


