import React from 'react'
import {Table} from 'element-react'
import {HiDownload} from 'react-icons/hi'
import {
    TableItemResponse, TableTopBrandItemResponse, TableTopProductItemResponse,
} from "../types/market";
import {TableSearchCloudItemResponse, TableSponsoredItemResponse} from "../types/seo";

interface TableChartProps {
    width?: string;
    height?: string;
    columns: ElementReact.TableColumn[];
    data: TableItemResponse[] | TableTopProductItemResponse[] | TableTopBrandItemResponse[] | TableSponsoredItemResponse[] | TableSearchCloudItemResponse[];

    onDownload?(): void
}

function TableChart(props: TableChartProps) {
    const {width = '100%', height = '400px', columns = [], data = [], onDownload} = props;

    return (
        <div className="relative overflow-y-scroll" style={{height}}>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>

            <div style={{width}}>
                <Table
                    className="h-full pt-4"
                    style={{width}}
                    columns={columns}
                    data={data}
                    border={true}
                />
            </div>
        </div>
    )
}

export default TableChart
