import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {PieChart, PieSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";
import {PieChartItem} from "../types/chart";

interface DoughnutChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    subtitle?: string;
    subtitleFontSize?: number;
    data: PieChartItem[]
}

type ECOption = echarts.ComposeOption<PieSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([PieChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer]);

function VerticalDoughnutChart(props: DoughnutChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 20, subtitle = '', subtitleFontSize = 24, data = []} = props;
    const option: ECOption = {
        legend: {
            bottom: '15%',
            textStyle: {
                fontSize: 20
            }
        },
        tooltip: {},
        series: [
            {
                type: "pie",
                center: ['50%', '40%'],
                radius: ["40%", "70%"],
                left: 'left',
                label: {
                    position: "inner",
                    formatter: '{d}%',
                    color: 'white'
                },
                labelLine: {
                    show: false
                },
                data
            }
        ]
    };
    useEffect(() => {
        if (!chartRef.current) return;

        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option)
    });

    const onDownload = async () => {
        if (!chartRef.current) return;

        await downloadPng(chartRef.current, title)
    };

    return (
        <div className="relative">
            <div className="absolute left-0 top-0">
                <h3 className="font-bold mr-4 text-gray-700" style={{fontSize: titleFontSize}}>{title}</h3>
                <h3 className="font-bold text-gray-500" style={{fontSize: subtitleFontSize}}>{subtitle}</h3>
            </div>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default VerticalDoughnutChart
