import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import 'echarts-wordcloud'
import {LineChart, BarChart} from "echarts/charts";
import {
    TitleComponent,
    GridComponent,
    TooltipComponent,
    LegendComponent,
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";

interface WordCloudChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    data: {name: string; value: string; color: string;}[];
    onClick?(value: string): void;
}

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer]);

function WordCloudChart(props: WordCloudChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, data = [], onClick} = props;
    const option = {
        title: {
            text: title,
            textStyle: {
                fontSize: titleFontSize
            }
        },
        rotationStep: 90,
        drawOutOfBound: false,

        grid: {
            backgroundColor: 'white',
            top: 0,
            right: 0,
            bottom: 0,
            left: 0
        },
        tooltip: {},
        series: [
            {
                type: 'wordCloud',
                data: data.map((it: {name: string; value: string; color: string;}) => ({
                    name: it.name,
                    value: it.value,
                    textStyle: {
                        color: it.color,
                    }
                }))
            }
        ]
    };

    useEffect(() => {
        if (!chartRef.current) return;

        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option)

        let lock = false;
        chart.on('click', (param) => {
            if (onClick && param) {
                if (lock) return;
                lock = true;
                onClick(param.name);
                setTimeout(() => lock = false, 1000)
            }
        })
    }, [data]);

    const onDownload = async () => {
        if (!chartRef.current) return;
        await downloadPng(chartRef.current, title)
    };

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default WordCloudChart
