import request from './https'
import { AxiosPromise } from "axios";
import {
    NewCommentBarChart,
    PriceDistributionBarLineChart,
    ProductBarChart,
    SearchParams,
    StockChartData,
    TopBrandBarChart,
    TopBrandPieChart,
    TableResponse,
    PopularPriceRangeResponse,
    PopularResolutionResponse,
    TableTopProductResponse,
    TableTopBrandResponse,
} from "../types/market";
import {FtiRankingChartResponse} from "../types/product";

export function getTopBrandBarChart(data: SearchParams) {
    return request({
        url:'/market/get_top_brand_bar_chart',
        method: 'GET',
        params: data
    }) as AxiosPromise<TopBrandBarChart>
}

