import React, {Dispatch, SetStateAction, useCallback, useEffect, useState} from 'react'
import {RiArrowDownSLine, RiSubtractLine} from 'react-icons/ri'
import {Button, Tag, Select} from 'element-react'
import {Adapter, OptionItem} from "../types/common";
import {CollapseProps, FormGroup, FormItem} from "../types/filter";
import {initDocumentCameraForm, initVideoProjectorForm, initWebcamForm} from "../utils/common";

export enum INPUT_TYPE {
    BUTTON,
    DROPDOWN,
    SCROLL_BAR,
}

interface CollapseItemProps {
    uuid: number,
    label: string,
    active: boolean,
    setActiveIndex: Dispatch<SetStateAction<number>>;
    children: JSX.Element;
}

function CollapseItem(props: CollapseItemProps) {
    const {
        uuid,
        label,
        active,
        setActiveIndex,
        children
    } = props;

    const handleClick = () => {
        setActiveIndex(active ? -1 : uuid)
    };

    return (
        <div>
            <div
                className="flex justify-between items-center py-2 cursor-pointer border-b-2 border-solid"
                onClick={handleClick}
            >
                <h2>{label}</h2>
                <div className="text-2xl">
                    {
                        active ? <RiSubtractLine/> : <RiArrowDownSLine/>
                    }
                </div>
            </div>
            <div className={`h-0 ${active && 'h-auto'} overflow-y-hidden`}>
                {children}
            </div>
        </div>
    )
}

const formatTags = (form: FormGroup[]): { label: string, keys: string[] }[] => {
    const ary: { label: string, keys: string[] }[] = [];
    form.forEach((group: FormGroup) => {
        group.items.forEach((item: FormItem) => {
            item.items.forEach((it: OptionItem) => {
                if (it.checked) {
                    ary.push({
                        label: it.label,
                        keys: [group.key, item.key, it.value]
                    })
                }
            })
        })
    });
    return ary
};

function Collapse(props: CollapseProps) {
    const {height = '300px', type = 'webcam', onFilter} = props;
    const [activeIndex, setActiveIndex] = useState(0);
    const [form, setForm] = useState<FormGroup[]>([]);

    const resetForm = useCallback(() => {
        const formAdapter: Adapter<() => FormGroup[]> = {
            'webcam': initWebcamForm,
            'video projector': initVideoProjectorForm,
            'document camera': initDocumentCameraForm,
            default: initWebcamForm
        };
        const formFn = formAdapter[type] || formAdapter.default;
        setForm(formFn())
    }, [type]);

    useEffect(() => {
        resetForm();
        setActiveIndex(0)
    }, [type, resetForm]);

    // 將 form 中 對應的 OptionItem 的 checked 改變。其他選項不動。
    const handleClickButton = (keys: string[], value: boolean) => {
        const _form = form.map((group: FormGroup): FormGroup => {
            return {
                ...group,
                items: group.items.map((item: FormItem): FormItem => {
                    return {
                        ...item,
                        items: item.items.map((it: OptionItem): OptionItem => {
                            const isTarget = group.key === keys[0] && item.key === keys[1] && it.value === keys[2];
                            return {
                                ...it,
                                checked: isTarget ? value : it.checked
                            }
                        })
                    }
                })
            }
        });
        setForm(_form)
    };

    const handleChangeSelect = (keys: string[], value: string) => {
        const _form = form.map((group: FormGroup): FormGroup => {
            return {
                ...group,
                items: group.items.map((item: FormItem): FormItem => {
                    return {
                        ...item,
                        items: item.items.map((it: OptionItem): OptionItem => {
                            const isTarget = group.key === keys[0] && item.key === keys[1] && it.value === value;
                            return {
                                ...it,
                                checked: isTarget
                            }
                        })
                    }
                })
            }
        });
        setForm(_form)
    };

    const handleClear = () => {
        resetForm()
    };

    const handleSearch = () => {
        const result: any = {};
        form.forEach((group: FormGroup) => {
            group.items.forEach((item: FormItem) => {
                let isFirst = true;
                const value = item.items.reduce((value: string, it: OptionItem): string => {
                    if (it.checked) {
                        if (!isFirst) {
                            value += `,`
                        }
                        isFirst = false;
                        value += `${it.value}`
                    }
                    return value
                }, '');
                if (value) {
                    result[item.key] = value
                }
            })
        });
        onFilter(result)
    };

    return (
        <div className="flex flex-col justify-between overflow-y-scroll" style={{height}}>
            <div className="pr-4">
                <h2 className="text-2xl font-medium pb-2">Advanced Filter</h2>
                {
                    form.map((group: FormGroup, index: number) => (
                        <CollapseItem
                            label={group.label}
                            uuid={index}
                            active={activeIndex === index}
                            setActiveIndex={setActiveIndex}
                        >
                            <div className="p-4">
                                {
                                    group.items.map((item: FormItem) => (
                                        <>
                                            <h2 className="my-2">{item.label}</h2>

                                            {
                                                item.type === INPUT_TYPE.BUTTON && item.items.map((it: OptionItem) => (
                                                    <Button
                                                        className="cursor-pointer"
                                                        type={it.checked ? "primary" : "info"}
                                                        size="small"
                                                        onClick={() => handleClickButton([group.key, item.key, it.value], !it.checked)}
                                                        style={{marginLeft: 0, margin: '4px 5px'}}
                                                    >
                                                        {it.label}
                                                    </Button>
                                                ))
                                            }
                                            {
                                                item.type === INPUT_TYPE.DROPDOWN && (
                                                    <Select
                                                        placeholder="Select"
                                                        value={
                                                            item.items.find(it => it.checked)
                                                                ? item.items.find(it => it.checked)!.value
                                                                : undefined
                                                        }
                                                        clearable
                                                        onChange={(value: string) => handleChangeSelect([group.key, item.key], value)}
                                                    >
                                                        {
                                                            item.items.map((it: OptionItem) =>
                                                                <Select.Option
                                                                    key={it.value}
                                                                    label={it.label}
                                                                    value={it.value}
                                                                />
                                                            )
                                                        }
                                                    </Select>
                                                )
                                            }
                                        </>
                                    ))
                                }
                            </div>
                        </CollapseItem>
                    ))
                }
                <div className="py-4 flex justify-end">
                    <Button type="info" onClick={handleClear}>Clear</Button>
                    <Button type="primary" onClick={handleSearch}>Search</Button>
                </div>
            </div>

            <div className="border border-2 p-4">
                {
                    formatTags(form).map((it: { label: string, keys: string[] }) => (
                        <Tag
                            className="mr-2"
                            type="primary"
                            closable={false}
                            onClose={() => handleClickButton(it.keys, false)}
                        >
                            {it.label}
                        </Tag>
                    ))
                }
            </div>
        </div>
    )
}

export default Collapse
