// 按照 Router 路徑名稱顯示
// 當數量只有一個的時候會補上 Dashboard
function Breadcrumb() {
    const location = useLocation();
    
    const pathNames: string[] = location.pathname.split('/').filter(name => !!name);

    return (
        <ElementBreadcrumb className="py-4 text-lg text-context" separator=">">
            {
                pathNames.map((name, index) =>
                    <ElementBreadcrumb.Item className="text-lg" key={index}>{name}</ElementBreadcrumb.Item>
                )
            }
            {
                pathNames.length < 2 &&
                <ElementBreadcrumb.Item className="text-lg">Dashboard</ElementBreadcrumb.Item>
            }
        </ElementBreadcrumb>
    )
}

export default Breadcrumb
