import request from './https'
import {AxiosPromise} from 'axios'
import {
    AmazonsChoice,
    AmazonsChoiceRate, SearchSearchCloudParams,
    SearchVolumeChartResponse,
    Sponsored,
    SponsoredAvg,
    SponsoredRate, TableSearchCloudResponse, TableSponsoredResponse, WordCloudResponse
} from "../types/seo";
import {SearchParams} from "../types/market";

export function getSponsored(data: SearchParams) {
    return request({
        url: '/SEO/sponsored',
        method: 'GET',
        params: data
    }) as AxiosPromise<Sponsored>
}

