import React, {useState} from 'react'
import {Card, Tooltip} from "element-react";
import defaultImageUrl from '../assets/default.jpeg'

export interface ProductCardProps {
    tagContext?: string;
    imageUrl?: string;
    name: string;
    price: number;
    code: string;
    'Resoluion & Frame Rate': [];
    Function: [];
    Certification: [];
    ranking: number;
    mission: string;
}

function ProductCard(props: ProductCardProps) {
    const {
        imageUrl = defaultImageUrl,
        name,
        price,
        code,
        'Resoluion & Frame Rate': resolution,
        Function,
        Certification,
        ranking,
        mission
    } = props;

    const [hover, setHover] = useState(false);
    return (
        <Card className="relative cursor-pointer" style={{overflow: 'visible'}}>
            <div onMouseEnter={() => setHover(true)} onMouseLeave={() => setHover(false)}>
                {/*Tag*/}
                <div className="absolute top-0 left-0 flex">
                    <div
                        className="pl-2 pr-4 py-1 bg-primary text-sm text-white text-center mr-4"
                        style={{width: '55px', clipPath: 'polygon(0% 0%, 0% 100%, 40px 100%, 100% 0%)'}}
                    >
                        <p>#{ranking}</p>
                    </div>

                    <div
                        className="pl-4 pr-2 py-1 bg-primary text-sm text-white"
                        style={{clipPath: 'polygon(15px 0%, 0% 100%, 100% 100%, 100% 0%)'}}
                    >
                        <p>{mission}</p>
                    </div>
                </div>

                <div className="bg-image my-4" style={{
                    width: '180px',
                    height: '180px',
                    backgroundImage: `url('${imageUrl}')`
                }} />

                <div className="flex justify-between items-center">
                    <Tooltip content={name} effect="light" className="overflow-hidden">
                        <h2 className="text-base text-context text-ellipsis">{name}</h2>
                    </Tooltip>
                    <p className="text-base text-context">${price}</p>
                </div>
                <p className="text-base text-context">{code}</p>

                {/*Hover: Information*/}
                {
                    (resolution.length || Function.length || Certification.length)
                        ?
                        <div
                            className={`absolute right-0 bottom-2 left-0 z-20 mt-4 px-5 pb-5 transform translate-y-full bg-white rounded ${hover ? '' : 'hidden'}`}
                            style={{boxShadow: '0 4px 4px 0 rgb(0 0 0 / 12%), 0 6px 6px 0 rgb(0 0 0 / 4%)'}}
                        >
                            {
                                resolution.length ? <p>Resolution: <span className="text-sm text-context">{resolution.join(', ')}</span></p> : ''
                            }
                            {
                                Function.length ? <p>Function: <span className="text-sm text-context">{Function.join(', ')}</span></p> : ''
                            }
                            {
                                Certification.length ? <p>Certification: <span className="text-sm text-context">{Certification.join(', ')}</span></p> : ''
                            }
                        </div>
                        : ''
                }
            </div>
        </Card>
    )
}

export default ProductCard


