import {Carousel} from 'element-react';
import React, {useRef} from 'react'
import {HiDownload} from "react-icons/hi";
import {downloadPng} from "../../utils/download";
import ProgressChart from "../../components/ProgressChart";
import SeoBarLineChart from "../../components/SeoBarLineChart";
import {AmazonsChoice, AmazonsChoiceRate} from "../../types/seo";
import {BarLine, BarLineChartData} from "../../types/market";

interface RankBoxProps {
    title: string;
    rank: number;
    color?: string
}

function RankBox(props: RankBoxProps) {
    const {title, rank, color = '#ACACAC'} = props;
    return (
        <div className="inline-flex flex-col justify-center items-center border-4 border-solid rounded py-4 px-8 mx-4"
             style={{color, borderColor: color, width: '300px'}}>
            {
                title === 'Amazon\'s Choice'
                    ? <h2 className="text-3xl pb-2 text-center">{title}</h2>
                    : <h2 className="text-5xl pb-2 text-center">{title}</h2>
            }
            <p className="text-2xl pb-2">Average ranking</p>
            <p className="text-8xl pb-2">{rank}</p>
        </div>
    )
}

interface RankComparisonProps {
    height?: string;
    title?: string;
    onClick?(value: number): void;
    data: AmazonsChoice | null;
    rate: AmazonsChoiceRate | null;
}

function formatBarLineData(data: AmazonsChoiceRate | null): BarLineChartData | null {
    console.log('data', data);
    if (!data) return null;
    const axis = data.info.x_axis;
    const series: BarLine[] = [
        {
            type: 'bar',
            name: 'Amazon\'s Choice',
            // @ts-ignore
            data: Object.keys(data.amazons_choice).map((key: string) => data.amazons_choice[key]),
            yAxisIndex: 0,
            stack: 'one',
        },
        {
            type: 'bar',
            name: 'Not Amazon\'s Choice',
            // @ts-ignore
            data: Object.keys(data.not_amazons_choice).map((key: string) => data.not_amazons_choice[key]),
            yAxisIndex: 0,
            stack: 'one',
        },
        {
            type: 'line',
            name: 'Amazon\'s Choice Rate',
            // @ts-ignore
            data: Object.keys(data.amazons_choice).map((key: string) => Math.round(data.amazons_choice[key] * 100 / (data.amazons_choice[key] + data.not_amazons_choice[key]))),
            yAxisIndex: 1,
        }
    ]
    return {axis, series}
}

function RankComparison(props: RankComparisonProps) {
    const shotWrapperRef = useRef<HTMLDivElement | null>(null)

    const {title = 'Title', height = '300px', data = null, rate = null, onClick} = props;

    const onDownload = async () => {
        if (!shotWrapperRef.current) return;
        await downloadPng(shotWrapperRef.current, title)
    }

    return (
        <div ref={shotWrapperRef} className="relative" style={{height}}>
            <Carousel className="mt-4" indicatorPosition="none" autoplay={false}>
                <Carousel.Item>
                    <h1 className="text-2xl">Amazon Choice Rank Comparison</h1>
                    <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>
                    <div className="flex justify-center mt-4" style={{height: '250px'}}>
                        {
                            data && <RankBox title="Amazon's Choice" rank={data.amazons_choice_avg} color="#2e78b4"/>
                        }
                        {
                            data && <RankBox title="Others" rank={data.unamazons_choice_avg}/>
                        }
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <SeoBarLineChart
                        height="300px"
                        width="728px"
                        title="Amazon's Choice Rate"
                        names={['Amount', 'Amazon\'s Choice Rate']}
                        data={formatBarLineData(rate)}
                        yAxisLabel={'Ranking'}
                    />
                </Carousel.Item>
            </Carousel>
            <div className="mt-8">
                {
                    data &&
                    <ProgressChart
                      title="Top Amazon Choice Ratio"
                      colors={["#2e78b4", "#ACACAC"]}
                      data={[{source: 'Amazon Choice', count: data.amazons_choice_rate}, {
                          source: 'Others',
                          count: data.unamazons_choice_rate
                      }]}
                      onClick={onClick}
                    />
                }
            </div>
        </div>
    )
}

export default RankComparison
