import React, {useEffect, useState} from 'react'
import dayjs from "dayjs";
import {Button, Card, DateRangePicker, Pagination, Select, Tooltip} from "element-react";
import {AiFillInfoCircle} from 'react-icons/ai'
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import Collapse from "../../components/Collapse";
import {initDate, initCategoryOptions, initRegionOptions} from "../../utils/common";
import RankComparison from "./RankComparison";
import {getSponsored, getSponsoredAvg, getSponsoredRate, getTableSponsored} from "../../apis/seo";
import {Sponsored, SponsoredAvg, SponsoredRate, TableSponsoredResponse} from "../../types/seo";
import {CHART_TYPE} from "../../types/common";
import {ProductsParams, ProductsResponse} from "../../types/product";
import {formatDate} from "../../utils/common";
import {getProducts} from "../../apis/product";
import ProductCards from "../../components/ProductCards";
import {SearchParams} from "../../types/market";
import TableChart from "../../components/TableChart";
import ProductsDownloadDialog from "../../components/ProductsDownloadDialog";

function Index() {
    const [chartType, setChartType] = useState(CHART_TYPE.CHART);
    const [visible, setVisible] = useState(false);

    const [category, setCategory] = useState('webcam');
    const [region, setRegion] = useState('US');
    const [date, setDate] = useState<ElementReactLibs.dateType[]>(initDate());
    const [filter, setFilter] = useState<any>({});
    const [sp, setSp] = useState<number>(1);

    const [page, setPage] = useState(1);
    const [productsPage, setProductsPage] = useState(1);

    const [sponsored, setSponsored] = useState<Sponsored | null>(null);
    const [table, setTable] = useState<TableSponsoredResponse | null>(null);
    const [rate, setRate] = useState<SponsoredRate | null>(null);
    const [avg, setAvg] = useState<SponsoredAvg | null>(null);
    const [products, setProducts] = useState<ProductsResponse | null>(null);

    const tableColumn: ElementReact.TableColumn[] = [
        {label: 'Product', prop: 'product'},
        {label: 'Brand', prop: 'brand', sortable: true},
        {label: 'Average Rank', prop: 'avg_rank'},
        {label: "Sponsored period", prop: "sponsored"},
    ];

    const categoryOptions = initCategoryOptions();
    const regionOptions = initRegionOptions();

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date);
            const params: SearchParams = {
                category,
                region,
                start_at,
                end_at,
                page,
                ...filter
            };
            const {data} = await getSponsored(params);
            const {data: table} = await getTableSponsored(params);
            const {data: rate} = await getSponsoredRate(params);
            const {data: avg} = await getSponsoredAvg(params);
            setSponsored(data);
            setTable(table);
            setRate(rate);
            setAvg(avg)
        };
        fetchData()
    }, [category, region, date, filter, page]);

    useEffect(() => {
        const fetchData = async () => {
            const [start_at, end_at] = formatDate(date);
            const params: ProductsParams = {
                category,
                region,
                page: productsPage,
                start_at,
                end_at,
                sp,
                ...filter,
            };
            const {data} = await getProducts(params);
            setProducts(data)
        };
        fetchData()
    }, [category, region, date, filter, productsPage, sp]);

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE);
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    };

    const handleDownloadProducts = async (asin: string[]) => {
        const [start_at, end_at] = formatDate(date);
        const params: ProductsParams = {
            category,
            region,
            start_at,
            end_at,
            dl: 1,
            sp,
            asin: asin.join(','),
            ...filter
        };
        const {request} = await getProducts(params);
        window.open(request.responseURL, '__blank')
    };

    const handleDownloadTable = async () => {
        const [start_at, end_at] = formatDate(date);
        const params: SearchParams = {
            category,
            region,
            start_at,
            end_at,
            page,
            sp,
            dl: 1,
            ...filter
        };
        const {request} = await getTableSponsored(params);
        window.open(request.responseURL, '__blank')
    };

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select"
                        onChange={(value: string) => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select"
                        onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DateRangePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0}
                    onChange={setDate}
                />
                <Tooltip
                    className="mr-4"
                    effect="dark"
                    content={
                        <div style={{maxWidth: '200px'}}>
                            The Stock level of each product is estimated by the maximum buying number limit in a single order. Identification between high stock and low stock may have certain deviation.
                        </div>
                    }
                    placement="bottom"
                >
                    <AiFillInfoCircle className="text-2xl"/>
                </Tooltip>
                <div className="text-2xl cursor-pointer" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <div className="flex">
                <div className="w-2/3 pr-2">
                    <Card className="mb-4">
                        {
                            chartType === CHART_TYPE.CHART &&
                            <RankComparison height="585px" data={sponsored} rate={rate} avg={avg}
                                            onClick={(value: number) => setSp(value)}/>
                        }
                        {
                            chartType === CHART_TYPE.TABLE && table &&
                            <>
                              <TableChart height="572px" columns={tableColumn} data={table.data.content}
                                          onDownload={handleDownloadTable}/>
                              <div className="flex justify-end">
                                <Pagination
                                  layout="prev, pager, next"
                                  pageCount={table.info.max_page}
                                  small={true}
                                  currentPage={table.data.current_page}
                                  onCurrentChange={(value: number) => setPage(value)}
                                />
                              </div>
                            </>
                        }
                    </Card>
                </div>

                <div className="w-1/3 pl-2">
                    <Card>
                        <Collapse height="600px" type={category} onFilter={setFilter}/>
                    </Card>
                </div>
            </div>

            {/* Product Cards */}
            <div className="mt-16">
                <div className="flex">
                    <p className="text-2xl mr-2">{sp ? 'Sponsored Product' : 'Not Sponsored Product'}</p>
                    <Button type="primary" onClick={() => setVisible(true)}>Download</Button>
                </div>
                {
                    products &&
                    <ProductCards
                      data={products.prods}
                      region={region}
                      page={products.info.current_page}
                      totalPage={products.info.max_page}
                      onChange={setProductsPage}
                    />
                }
            </div>

            <ProductsDownloadDialog
                visible={visible}
                setVisible={setVisible}
                params={{
                    category,
                    region,
                    date,
                    sp,
                    ...filter
                }}
                onDownload={handleDownloadProducts}
            />
        </div>
    )
}

export default Index
