import React, {useEffect, useState} from 'react';
import {Button, Dialog, Pagination, Tag} from "element-react";
import {ProductItemResponse, ProductsResponse} from "../types/product";
import {getProducts} from "../apis/product";
import {Adapter, iDialog} from "../types/common";
import {ProductCardProps} from "./ProductCard";
import ProductCard from "./ProductCard";
import {useUpdateEffect} from "../utils/hook";
import {BsSquare, BsCheckSquare} from 'react-icons/bs'
import {formatDate} from "../utils/common";

interface ProductDialogProps extends iDialog {
    params: any;
    onDownload: (asin: string[]) => {};
}

function ProductsDownloadDialog(props: ProductDialogProps) {
    const {visible, setVisible, params, onDownload} = props;
    const [products, setProducts] = useState<ProductsResponse | null>(null);
    const [asinValue, setAsinValue] = useState<string[]>([]);
    const [page, setPage] = useState(1);
    const [totalPage, setTotalPage] = useState(1);

    const fetchData = async () => {
        if (params.date) {
            const [start_at, end_at] = formatDate(params.date);
            params.start_at = start_at;
            params.end_at = end_at;
        }
        if (params.stock) {
            const adapter: Adapter<string> = {
                'Available': 'unknown',
                'High stock': 'height',
                'Low stock': 'low',
                'Out of stock': 'out',
                default: ''
            };
            params.stock = adapter[params.stock]
        }
        const {data} = await getProducts({...params, page});
        setProducts(data);
        setTotalPage(data.info.max_page);
    };

    useEffect(() => {
        if (!visible) return;
        setPage(1);
        setAsinValue([]);
        if (page === 1) fetchData()
    }, [visible]);

    useUpdateEffect(() => {
        fetchData()
    }, [page]);

    const handleSelectProduct = (asin: string) => {
        if (asinValue.includes(asin)) {
            setAsinValue(asinValue.filter((it: string) => it !== asin))
        } else {
            setAsinValue([...asinValue, asin])
        }
    };

    const handleCloseTag = (asin: string) => {
        setAsinValue(asinValue.filter((it: string) => it !== asin))
    };

    return (
        <Dialog
            top="30px"
            size="large"
            visible={visible}
            onCancel={() => setVisible(false)}
        >
            <Dialog.Body>
                <div className="w-full overflow-x-scroll">
                    <div className="main-container">
                        {products && products.prods.length
                            ? (
                                <div className="flex flex-wrap -mx-1 mt-4">
                                    {
                                        products.prods.map((it: ProductItemResponse) => {
                                            const props: ProductCardProps = {
                                                name: it.name,
                                                price: it.price,
                                                code: it.ASIN,
                                                imageUrl: it.image_url[0],
                                                'Resoluion & Frame Rate': it['Resoluion & Frame Rate'],
                                                Function: it.Function,
                                                Certification: it.Certification,
                                                ranking: it.ranking,
                                                mission: it.mission,
                                            };
                                            return (
                                                <div
                                                    className="relative mx-1 mb-2"
                                                    style={{width: 'calc(20% - 0.5rem)'}}
                                                    onClick={() => handleSelectProduct(it.ASIN)}
                                                >
                                                    <div className="absolute -right-1 -top-1 z-50 text-lg bg-white cursor-pointer">
                                                        { asinValue.includes(it.ASIN) ? <BsCheckSquare/> : <BsSquare /> }
                                                    </div>
                                                    <ProductCard {...props}/>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            )
                            : (
                                <div className="flex justify-center items-center px-4 py-8">
                                    <h3 className="text-gray-400">No Data</h3>
                                </div>
                            )
                        }
                        {
                            totalPage >= 1 && (
                                <div className="flex justify-center my-8">
                                    <Pagination
                                        layout="prev, pager, next"
                                        pageCount={totalPage}
                                        currentPage={page}
                                        onCurrentChange={(value: number) => setPage(value)}/>
                                </div>
                            )
                        }
                    </div>
                </div>

                <div>
                    <h3 className="text-lg">Selected Products</h3>
                    <div className="border border-2 p-4 mt-2" style={{minHeight: '100px'}}>
                        {
                            asinValue.map((asin: string) => (
                                <Tag
                                    className="mr-2"
                                    type="primary"
                                    closable={false}
                                    onClose={() => handleCloseTag(asin)}
                                >
                                    {asin}
                                </Tag>
                            ))
                        }
                    </div>
                    <div className="mt-2 flex justify-end">
                        <Button type="primary" onClick={() => onDownload(asinValue)}>Download</Button>
                    </div>
                </div>
            </Dialog.Body>
        </Dialog>
    )
}

export default ProductsDownloadDialog
