import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {PieChart, PieSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";
import {AiOutlineClose} from "react-icons/ai";

interface PercentageDoughnutChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    name?: string;
    brand?: string;
    color?: string;
    percentage: number,
    onClear: () => {}
}

type ECOption = echarts.ComposeOption<PieSeriesOption | TitleComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([PieChart, TitleComponent, TooltipComponent, LegendComponent, CanvasRenderer]);

function PercentageDoughnutChart(props: PercentageDoughnutChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const chartWrapperRef = useRef<HTMLDivElement | null>(null);

    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24,  name = 'Done', percentage = 0, brand, color, onClear} = props;
    const option: ECOption = {
        tooltip: {},
        series: [
            {
                type: "pie",
                radius: ["55%", "85%"],
                top: '15%',
                label: {
                    show: false,
                    position: "center"
                },
                labelLine: {
                    show: false
                },
                itemStyle: {
                    // @ts-ignore
                    color(params: any) {
                        if (params.dataIndex) return '#acacac';
                        return color;
                    }
                },
                data: [
                    {name, value: percentage},
                    {name: 'None', value: 100 - percentage},
                ]
            }
        ]
    };

    useEffect(() => {
        if (!chartRef.current) return;

        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option)
    });

    const onDownload = async () => {
        if (!chartWrapperRef.current) return;
        await downloadPng(chartWrapperRef.current, title)
    };

    return (
        <div className="relative">
            <div className="flex items-center absolute left-0 top-0">
                <h3 className="font-bold mr-4 text-gray-700" style={{fontSize: titleFontSize}}>{title}</h3>
            </div>
            {
                brand && (
                    <div
                        className="absolute left-0 top-7 flex items-center rounded p-1 z-50 cursor-pointer"
                        style={{backgroundColor: color, fontSize: '.6rem'}}
                        onClick={onClear}
                    >
                        { brand }
                        <AiOutlineClose className="ml-1" />
                    </div>
                )
            }
            <div ref={chartWrapperRef}>
                <div ref={chartRef} style={{width, height}}/>
                <p className="absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-primary text-3xl"
                   style={{top: '58%'}}
                >{percentage}%</p>
            </div>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>
        </div>
    )
}

export default PercentageDoughnutChart
