import React from 'react'
import {Card} from "element-react";

interface InfoCardProps {
    height?: string;
    title: string;
    context: string;
    reviewsContext: string
}

function InfoCard(props: InfoCardProps) {
    const {height = '100px', title, context, reviewsContext} = props
    return (
        <Card>
            <div style={{height}}>
                <p className="text-xl font-medium text-context pb-4">{title}</p>
                <p className="text-xl font-medium text-context">
                    <span className="text-4xl font-bold text-black">{context}</span> with <span
                    className="text-black">{reviewsContext}</span> new reviews
                </p>
            </div>
        </Card>
    )
}

export default InfoCard
