import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, LineSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";
import {SponsoredAvg} from "../types/seo";

interface BarLineChartProps {
    width?: string;
    height?: string;
    data: SponsoredAvg | null
}

type ECOption = echarts.ComposeOption<LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer])

function takeLegendData(data: SponsoredAvg) {
    const target = data.data.reduce((target: Object, obj: Object) => Object.keys(obj).length > Object.keys(target).length ? obj : target, {})
    return Object.keys(target).filter(it => it !== 'mission').map((key: string) => `Start at ${key}`)
}

function takeSeriesData(data: SponsoredAvg) {
    const target = data.data.reduce((target: Object, obj: Object) => Object.keys(obj).length > Object.keys(target).length ? obj : target, {})
    return Object.keys(target).filter(it => it !== 'mission').map((key: string) => ({
        type: 'line',
        name: `Start at ${key}`,
        // @ts-ignore
        data: data.data.map((obj: Object) => obj[key] ? obj[key] : null)
    }))
}

function AverageChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', data = null} = props;
    const title = `Average Rank Increase between ${data ? data.info.x_axis[0] : ''} to ${data ? data.info.x_axis[data.info.x_axis.length - 1] : ''} sponsored`
    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: 18
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 70,
            right: 50,
            left: 50
        },
        legend: {
            data: data ? takeLegendData(data) : [],
            bottom: 0
        },
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: data ? data.info.x_axis : []
            }
        ],
        yAxis: [
            {
                type: 'value',
                name: 'Average Rank'
            },
        ],
        // @ts-ignore
        series: data ? takeSeriesData(data) : []
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30r" onClick={onDownload}/>

            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default AverageChart
