import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {BarChart, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";

interface VerticalProgressBarChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    data: [string[], { name: string, data: string, value: number }[], number[]];
    padding: string;

    onClick?(value: string): void;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption>;

echarts.use([BarChart, TitleComponent, GridComponent, TooltipComponent, CanvasRenderer])

function VerticalProgressBarChart(props: VerticalProgressBarChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null)
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, data, onClick, padding = ''} = props;
    const [yAxisData, seriesData, ranks] = data;

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: titleFontSize
            }
        },
        grid: {
            top: 70,
            right: 20,
            bottom: 80,
            left: 50,
        },
        tooltip: {
            trigger: 'axis',
            position: 'top',
            confine: true,
            formatter: "<div style='display:block; word-break: break-all;word-wrap: break-word; white-space:pre-wrap; width: 400px;'>" + "{b} " + "</div>" + " <br/>{a}: {c}"
        },
        xAxis: [
            {
                type: 'category',
                data: yAxisData,
                axisLabel: {
                    interval: 0,
                    rotate: 20,
                    formatter(value: any) {
                        let res = value;
                        if (res.length > 9) {
                            res = res.substring(0, 8) + "...";
                        }
                        return res;
                    }
                }
            }
        ],
        yAxis: [
            {
                type: 'value',
                splitLine: {
                    show: false
                }
            }
        ],
        series: [
            {
                type: 'bar',
                name: 'Times',
                yAxisIndex: 0,
                showBackground: true,
                data: seriesData.map(it => it.value),
                barWidth: 20,
                itemStyle: {
                    color: '#ffc000',
                }
            },
        ]
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
        chart.on('click', (params) => {
            if (onClick) {
                onClick(seriesData[params.dataIndex].data)
            }
        })
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>
            <div ref={chartRef} style={{width, height}}/>
            <h3 className="absolute left-0 bottom-0 text-primary">Avg<br/>Rank</h3>
            <div
                className="absolute left-0 bottom-0 w-full flex"
                style={{padding}}
            >
                {
                    ranks.map((rank: number, index: number) => <span className="text-primary text-center" key={index} style={{width: `${100 / ranks.length}%`}}>#{rank}</span>)
                }
            </div>
        </div>
    )
}

export default VerticalProgressBarChart
