import request from "./https";
import {AxiosPromise} from "axios";
import {ProductParams, ProductResponse, ProductsParams, ProductsResponse} from "../types/product";
import {SearchParams} from "../types/market";

export function getProducts(data: ProductsParams) {
    return request({
        url:'/products',
        method: 'GET',
        params: data
    }) as AxiosPromise<ProductsResponse>
}

export function getProduct(data: ProductParams) {
    return request({
        url:'/product',
        method: 'GET',
        params: data
    }) as AxiosPromise<ProductResponse>
}

export function getProductsDownloadUrl(data: SearchParams) {
    return request({
        url:'/products_xlsx',
        method: 'GET',
        params: data
    })
}
