import React, {useEffect, useMemo, useState} from 'react'
import dayjs from 'dayjs'
import {Card, DatePicker, Pagination, Select} from "element-react";
import {FaListAlt} from 'react-icons/fa'
import {RiBarChartBoxFill} from 'react-icons/ri'

import {initDate, initCategoryOptions, initRegionOptions} from "../utils/common";
import HorizontalBarChart from "../components/HorizontalBarChart";
import WordCloudChart from '../components/WordCloudChart';
import {CHART_TYPE} from "../types/common";
import {formatDate} from "../utils/common";
import {SearchParams} from "../types/market";
import {getKeywordWordCloud, getSearchVolumeBarChart, getTableSearchCloud} from "../apis/seo";
import {SearchSearchCloudParams, SearchVolumeChartResponse, TableSearchCloudResponse} from "../types/seo";
import TableChart from "../components/TableChart";
import {useUpdateEffect} from "../utils/hook";

function formatData(data: SearchVolumeChartResponse): [string[], { data: string, value: number }[]] {
    const seriesData = data.data.map((it: string) => ({data: it, value: parseInt(it, 10)}));
    return [data.x_axis, seriesData]
}

function KeywordAnalysis() {
    const [chartType, setChartType] = useState(CHART_TYPE.CHART);

    const [category, setCategory] = useState('webcam');
    const [region, setRegion] = useState('US');
    const [date, setDate] = useState<ElementReactLibs.dateType>(initDate()[1]);

    const [page, setPage] = useState(1);

    const [words, setWords] = useState<{ name: string, value: string, color: string; }[]>([]);
    const [table, setTable] = useState<TableSearchCloudResponse | null>(null);
    const [volume, setVolume] = useState<SearchVolumeChartResponse | null>(null);
    const [word, setWord] = useState('');

    const categoryOptions = initCategoryOptions();
    const regionOptions = initRegionOptions();

    const tableColumn: ElementReact.TableColumn[] = [
        {label: "Keyword", prop: "Phrase"},
        {label: 'Search Volume', prop: 'Search Volume'},
        {
            label: 'Search Volume Trend', prop: 'Search Volume Trend', width: 190,
            render(data: any) {
                const trend: string = data['Search Volume Trend'];
                const isNegative = trend.includes('-');
                return (
                    <span style={{color: isNegative ? 'red' : 'green'}}>
                        {trend}
                    </span>
                )
            }
        },
        {label: 'Competing Products', prop: 'Competing Products'},
    ];

    const fetchChart = async () => {
        const [start_at, end_at] = formatDate([date, date]);
        const params: SearchParams = {category, region, start_at, end_at};
        const {data: words} = await getKeywordWordCloud(params);
        setWords(words.data);
    };

    const fetchTable = async () => {
        const [start_at] = formatDate([date, date]);
        const params: SearchSearchCloudParams = {
            category,
            start_at,
            page,
            dl: 0
        };
        const {data} = await getTableSearchCloud(params);
        setTable(data)
    };

    const fetchVolume = async () => {
        const [start_at, end_at] = formatDate([date, date]);
        const params: SearchParams = {category, region, start_at, end_at};
        if (word) params.word = word;
        const {data: volume} = await getSearchVolumeBarChart(params);
        setVolume(volume)
    };

    useEffect(() => {
        setPage(1);
        setWord('');

        if (chartType === CHART_TYPE.CHART) fetchChart();
        if (chartType === CHART_TYPE.TABLE && page === 1) fetchTable();
        if (!word) fetchVolume();
    }, [region, category, date]);

    useUpdateEffect(() => {
        if (chartType === CHART_TYPE.CHART) fetchChart();
        if (chartType === CHART_TYPE.TABLE) fetchTable();
    }, [chartType, page]);

    useUpdateEffect(() => {
        fetchVolume()
    }, [word]);

    const onChartTypeChange = () => {
        if (chartType === CHART_TYPE.CHART) setChartType(CHART_TYPE.TABLE);
        if (chartType === CHART_TYPE.TABLE) setChartType(CHART_TYPE.CHART)
    };

    const handleDownloadTable = async () => {
        const [start_at] = formatDate([date, date]);
        const params: SearchSearchCloudParams = {
            category,
            start_at,
            dl: 1
        };
        const {request} = await getTableSearchCloud(params);
        window.open(request.responseURL, '__blank')
    };

    return (
        <div>
            {/* Search Bar */}
            <div className="flex items-center mb-4">
                <Select className="mr-4" value={category} placeholder="Select"
                        onChange={(value: string) => setCategory(value)}>
                    {
                        categoryOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <Select className="mr-4" value={region} placeholder="Select"
                        onChange={(value: string) => setRegion(value)}>
                    {
                        regionOptions.map(it => {
                            return <Select.Option key={it.value} label={it.label} value={it.value}/>
                        })
                    }
                </Select>
                <DatePicker
                    isReadOnly={true}
                    className="mr-4"
                    value={date}
                    disabledDate={(_date: Date) => dayjs(_date).day() !== 0 || dayjs(_date).isBefore('2021-11-21', 'day')}
                    onChange={setDate}
                />
                <div className="text-2xl cursor-pointer mr-4" onClick={onChartTypeChange}>
                    {
                        chartType === CHART_TYPE.CHART && <FaListAlt/>
                    }
                    {
                        chartType === CHART_TYPE.TABLE && <RiBarChartBoxFill/>
                    }
                </div>
            </div>

            <Card className="mb-4" style={{minHeight: '642px'}}>
                {
                    chartType === CHART_TYPE.CHART &&
                    <WordCloudChart
                        title="Keyword Cloud"
                        height="600px"
                        data={words}
                        onClick={(value: string) => setWord(value)}
                    />
                }
                {
                    chartType === CHART_TYPE.TABLE && table &&
                    <>
                      <TableChart height="572px" columns={tableColumn} data={table.data.content}
                                  onDownload={handleDownloadTable}/>
                      <div className="flex justify-end">
                          <Pagination
                              layout="prev, pager, next"
                              pageCount={table.info.max_page}
                              small={true}
                              currentPage={table.info.current_page}
                              onCurrentChange={(value: number) => setPage(value)}
                          />
                      </div>
                    </>
                }
            </Card>

            <Card style={{minHeight: '642px'}}>
                {
                    volume &&
                    <HorizontalBarChart
                      title="Search Volume"
                      height="600px"
                      data={formatData(volume)}
                      dataItemName="Products"
                    />
                }
            </Card>
        </div>
    )
}

export default KeywordAnalysis
