import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, BarChart, LineSeriesOption, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'

import {downloadPng} from "../utils/download";
import {StockChartData, StockItem} from "../types/market";

interface BarLineChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    data: StockChartData | null;
    onClick?(value: string): void;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer]);

function formatData(data: StockChartData | null): [string[], string[], any, number[]] {
    if (!data) return [[], [], {}, []];
    const legend = [
        'Available, instock unknown',
        'High stock',
        'Low stock',
        'Out of stock'
    ];
    const yAxisData = data.info.x_axis!;
    const totals = data.items.map((it: StockItem) =>
        // @ts-ignore
        legend.reduce((sum: number, key: string) => sum + it[key], 0)
    );
    const seriesData = legend.reduce((obj: any, key: string) => {
        // @ts-ignore
        obj[key] = data.items.map((it: StockItem, index: number) => it[key] * 100 / totals[index]);
        return obj
    }, {});
    return [legend, yAxisData, seriesData, totals]
}

function StockChart(props: BarLineChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, data = null, onClick} = props;

    const [legend, yAxisData, seriesData, totals] = formatData(data);

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: titleFontSize
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 70,
            right: 50,
            left: 50
        },
        legend: {
            data: legend,
            bottom: 0
        },
        tooltip: {
            trigger: 'axis',
            formatter(params: any) {
                return `
                    ${params[0].name} <br />
                    ${params.map((it: any) => `
                        <div  style="display: flex; justify-content: space-between;">
                            <span style="margin-right: 15px;">${it.marker} ${it.seriesName}</span> ${Math.round(it.value * totals[it.dataIndex] / 100)} <br />
                        </div>
                    `)
                    .join("")}
                `;
            }
        },
        xAxis: [
            {
                type: 'category',
                boundaryGap: false,
                data: yAxisData
            }
        ],
        yAxis: [
            {
                type: 'value',
                min: 0,
                max: 100,
                axisLabel: {
                    formatter: "{value} %"
                }
            },
        ],
        series: [
            {
                type: 'line',
                name: 'Out of stock',
                stack: 'stack',
                data: seriesData['Out of stock'],
                symbolSize: 8,
                itemStyle: {
                    color: '#FF5757'
                },
                areaStyle: {
                    color: '#FF5757'
                }
            },
            {
                type: 'line',
                name: 'Low stock',
                stack: 'stack',
                data: seriesData['Low stock'],
                symbolSize: 8,
                itemStyle: {
                    color: '#FFC000'
                },
                areaStyle: {
                    color: '#FFC000'
                }
            },
            {
                type: 'line',
                name: 'High stock',
                stack: 'stack',
                data: seriesData['High stock'],
                symbolSize: 8,
                itemStyle: {
                    color: '#9DC3E6'
                },
                areaStyle: {
                    color: '#9DC3E6'
                }
            },
            {
                type: 'line',
                name: 'Available, instock unknown',
                stack: 'stack',
                data: seriesData['Available, instock unknown'],
                symbolSize: 8,
                itemStyle: {
                    color: '#ACACAC'
                },
                areaStyle: {
                    color: '#ACACAC'
                }
            }
        ]
    };

    useEffect(() => {
        if (!chartRef.current) return;

        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option);

        let lock = false;
        chart.on('click', (params) => {
            if (onClick && data && params) {
                if (lock) return;
                const date = data.info.x_axis![params.dataIndex];
                const name = params.seriesName;
                onClick(`${date}, ${name}`);
                setTimeout(() => lock = false, 1000)
            }
        })
    });

    const onDownload = async () => {
        if (!chartRef.current) return;

        await downloadPng(chartRef.current, title)
    };

    return (
        <div className="relative">
            <div ref={chartRef} style={{width, height}}/>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>
        </div>
    )
}

export default StockChart
