import React, {useEffect, useRef, useState} from 'react'
import * as echarts from 'echarts/core'
import {LineChart, BarChart, LineSeriesOption, BarSeriesOption} from "echarts/charts";
import {
    TitleComponent,
    TitleComponentOption,
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption,
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {HiDownload} from 'react-icons/hi'
import {downloadPng} from "../../utils/download";
import {BarLine, BarLineChartData} from "../../types/market";
import {takeIntervalAxisSetting} from "../../utils/chart";
import {Button} from "element-react";
import {Adapter} from "../../types/common";

interface BarLineChartProps {
    width?: string;
    height?: string;
    data: BarLineChartData | null;
}

type ECOption = echarts.ComposeOption<BarSeriesOption | LineSeriesOption | TitleComponentOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([LineChart, BarChart, TitleComponent, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer])

const adapter: Adapter<string> = {
    weight: 'Weight',
    value: 'Value',
    default: ''
}

function BarLineChart(props: BarLineChartProps) {
    const {width = '100%', height = '400px', data = null} = props;
    const chartRef = useRef<HTMLDivElement | null>(null);

    const [type, setType] = useState('value');

    function getLegend(name: string) {
        if (!data) return name;

        const target = data.series.find((it: BarLine) => it.name === name)
        if (!target) return name;

        const total = target.data.reduce((result, num) => result + num, 0).toFixed(2);
        return `${name}, total: ${total}`
    }

    const title = `${type === 'value' ? 'Value' : 'Weight'} and Bill of Lading`;
    const leftAxisSetting = takeIntervalAxisSetting(data ? data.series.filter((it: BarLine) => it.yAxisIndex === 0 && it.name === adapter[type]).reduce((arr: number[], list: BarLine) => [...arr, ...list.data], []) : [])
    const rightAxisSetting = takeIntervalAxisSetting(data ? data.series.filter((it: BarLine) => it.yAxisIndex === 1).reduce((arr: number[], list: BarLine) => [...arr, ...list.data], []) : [])

    const option: ECOption = {
        title: {
            text: title,
            textStyle: {
                fontSize: 20
            }
        },
        grid: {
            backgroundColor: 'white',
            top: 90,
            right: 50,
            left: 70,
            bottom: 80
        },
        legend: {
            bottom: 0,
            formatter(name) {
                return getLegend(name)
            }
        },
        tooltip: {
            trigger: 'axis'
        },
        xAxis: [
            {
                type: 'category',
                data: data ? data.axis : [],
                axisLabel: {
                    interval: 0,
                    rotate: 15,
                    formatter(name: any) {
                        const MAX_LENGTH = 15
                        if (name.length <= MAX_LENGTH) return name
                        return name.slice(0, MAX_LENGTH - 1) + '...'
                    }
                },
            }
        ],
        yAxis: [
            {
                type: 'value',
                name: type === 'value' ? 'Value\n(USD)' : 'Weight\n(Kg)',
                min: leftAxisSetting.min,
                max: leftAxisSetting.max,
                interval: leftAxisSetting.interval
            },
            {
                type: 'value',
                name: 'BOL',
                min: rightAxisSetting.min,
                max: rightAxisSetting.max,
                interval: rightAxisSetting.interval,
            }
        ],
        series: data ? data.series.filter((it: BarLine) => it.yAxisIndex !== 0 || it.name === adapter[type]) : []
    }

    useEffect(() => {
        if (!chartRef.current) return

        const chart = echarts.init(chartRef.current, 'customed')
        chart.setOption(option)
    })

    const onDownload = async () => {
        if (!chartRef.current) return

        await downloadPng(chartRef.current, title)
    }

    return (
        <div className="relative">
            <div className="flex absolute right-4 top-0 z-30">
                <Button type={type === 'value' ? "primary" : undefined} size="small"
                        onClick={() => setType('value')}>Value</Button>
                <Button type={type === 'weight' ? "primary" : undefined} size="small"
                        onClick={() => setType('weight')}>Weight</Button>
                <HiDownload className="text-3xl cursor-pointer ml-2" onClick={onDownload}/>
            </div>
            <div ref={chartRef} style={{width, height}}/>
        </div>
    )
}

export default BarLineChart
