import React, {useEffect, useRef} from 'react'
import * as echarts from 'echarts/core'
import {PieChart, PieSeriesOption} from "echarts/charts";
import {
    GridComponent,
    GridComponentOption,
    TooltipComponent,
    TooltipComponentOption,
    LegendComponent,
    LegendComponentOption
} from "echarts/components";
import {CanvasRenderer} from "echarts/renderers";
import {downloadPng} from "../utils/download";
import {HiDownload} from "react-icons/hi";
import {PieChartItem} from "../types/chart";

interface DoughnutChartProps {
    width?: string;
    height?: string;
    title?: string;
    titleFontSize?: number;
    subtitle?: string;
    subtitleFontSize?: number;
    data: PieChartItem[];
    hasLegend?: Boolean;
    onClick?(value: string): void;
}

type ECOption = echarts.ComposeOption<PieSeriesOption | GridComponentOption | TooltipComponentOption | LegendComponentOption>;

echarts.use([PieChart, GridComponent, TooltipComponent, LegendComponent, CanvasRenderer]);

function DoughnutChart(props: DoughnutChartProps) {
    const chartRef = useRef<HTMLDivElement | null>(null);
    const {width = '100%', height = '400px', title = 'Title', titleFontSize = 24, subtitle = '', subtitleFontSize = 20, data = [], hasLegend = false, onClick} = props;
    const option: ECOption = {
        tooltip: {},
        series: [
            {
                type: "pie",
                center: ['50%', '50%'],
                radius: ["40%", "70%"],
                left: 'left',
                label: {
                    position: "inner",
                    formatter: '{d}%',
                    color: 'white'
                },
                labelLine: {
                    show: false
                },
                data
            }
        ]
    };

    if (hasLegend) {
        option.legend = {
            orient: 'vertical',
            right: '10%',
            top: 'middle'
        };
        // @ts-ignore
        option.series[0].center[0] = '35%'
    }

    useEffect(() => {
        if (!chartRef.current || !data.length) return;
        const chart = echarts.init(chartRef.current, 'customed');
        chart.setOption(option);
        chart.on('click', (params) => {
            if(onClick && params) {
                onClick(params.name)
            }
        })
    }, [data, option, onClick]);

    const onDownload = async () => {
        if (!chartRef.current) return;
        await downloadPng(chartRef.current, title)
    };

    return (
        <div className="relative">
            <div className="flex items-center absolute left-0 top-0">
                <h3 className="font-bold mr-4 text-gray-700" style={{fontSize: titleFontSize}}>{title}</h3>
                <h3 className="font-bold text-gray-500" style={{fontSize: subtitleFontSize}}>{subtitle}</h3>
            </div>
            <div ref={chartRef} style={{width, height}}/>
            <HiDownload className="absolute right-0 top-0 text-3xl cursor-pointer z-30" onClick={onDownload}/>
        </div>
    )
}

export default DoughnutChart
