import request from './https'
import {AxiosPromise} from 'axios'
import {FilterResponse, PieResponse, SearchCustomParams, TableResponse} from "../types/custom";
import {BarLineChartData} from "../types/market";

export function getCustomFilters() {
    return request({
        url:'/customs/importer/filters',
        method: 'GET',
    }) as AxiosPromise<FilterResponse>
}

export function getCustomValuePie(data: SearchCustomParams) {
    const params: any = {}
    if (data.exporter) params.exporter = data.exporter;
    if (data.hs_code) params.hs_code = data.hs_code;
    if (data.start_at) params.start_at = data.start_at;
    if (data.end_at) params.end_at = data.end_at;
    return request({
        url:'/customs/importer/value_pie',
        method: 'GET',
        params
    }) as AxiosPromise<PieResponse>
}

