export function getCustomTableDownload(data: SearchCustomParams) {
    const params: any = {}
    if (data.exporter) params.exporter = data.exporter;
    if (data.hs_code) params.hs_code = data.hs_code;
    if (data.start_at) params.start_at = data.start_at;
    if (data.end_at) params.end_at = data.end_at;
    if (data.page) params.page = data.page;
    return request({
        url:'/customs/importer/value_bill_table',
        method: 'GET',
        params: { ...params, is_excel_download: true }
    }) as AxiosPromise<string>
}

