import React, {useEffect, useState} from 'react';
import {Card, Carousel, Dialog, Rate, Tooltip} from "element-react";
import {FaExternalLinkAlt} from 'react-icons/fa'
import defaultImage from '../../assets/default.jpeg'
import Review from "./charts/Review";
import Rank from "./charts/Rank";
import StockLevel from "./charts/StockLevel";
import Price from "./charts/Price";
import {iDialog} from '../../types/common'
import {getProduct} from "../../apis/product";
import {ProductParams, ProductResponse} from "../../types/product";

interface ProductDialogProps extends iDialog {
    region: string;
    asin: string;
}

function ProductDialog(props: ProductDialogProps) {
    const {visible, setVisible, region = '', asin = ''} = props;
    const [product, setProduct] = useState<ProductResponse | null>(null)

    useEffect(() => {
        if (!visible || !region || !asin) return;
        const fetchData = async () => {
            const params: ProductParams = {
                region,
                asin
            }
            const {data} = await getProduct(params);
            setProduct(data)
        }
        fetchData()
    }, [visible])

    const linkToAmazonHandler = () => {
      window.open(product ? product.product_detail.url : '')
    }

    return (
        <Dialog
            customClass="w-dialog h-dialog"
            visible={visible}
            onCancel={() => setVisible(false)}
        >
            <Dialog.Body>
                {
                    product && (
                        <>
                            <div className="flex">
                                {/*Product Info*/}
                                <div className="w-1/3" style={{height: '450px'}}>
                                    <Tooltip content={product.product_detail.name} effect="light">
                                      <h1 className="text-3xl text-context text-ellipsis">{product.product_detail.name}</h1>
                                    </Tooltip>
                                    {/*<h2 className="text-3xl text-primary">{product.product_detail.ASIN}</h2>*/}
                                    <div className="bg-image w-full mt-4" style={{
                                        height: '350px',
                                        backgroundImage: `url('${product.product_detail.image_url[0] || ''}')`
                                    }} />
                                    <div className="flex justify-between items-center">
                                        <Rate disabled={true} value={parseFloat(product.product_detail.average_rating)} showText={true}/>
                                        <p className="text-2xl text-context">${product.product_detail.price}</p>
                                    </div>
                                </div>

                                {/*Swipers*/}
                                <div className="w-2/3" style={{height: '450px'}}>
                                    <Carousel indicatorPosition="none" autoplay={false} height={"450px"}>
                                        <Carousel.Item>
                                            <Price axis={product.price_chart.x_axis} data={product.price_chart.data}/>
                                        </Carousel.Item>
                                        <Carousel.Item>
                                            <Review axis={product.review_number.x_axis} data={product.review_number.data}/>
                                        </Carousel.Item>
                                        <Carousel.Item>
                                            <StockLevel axis={product.instock.x_axis} data={product.instock.data}/>
                                        </Carousel.Item>
                                        {/*<Carousel.Item>*/}
                                            {/*<Keyword />*/}
                                        {/*</Carousel.Item>*/}
                                        <Carousel.Item>
                                            <Rank axis={product.ranking.x_axis} data={product.ranking.data}/>
                                        </Carousel.Item>
                                    </Carousel>
                                </div>
                            </div>

                            {/*Footer*/}
                            <div className="flex justify-between items-end" style={{height: '50px'}}>
                                <p className="text-xl text-context">ASIN: {product.product_detail.ASIN}</p>
                                <div className="flex items-center cursor-pointer" onClick={linkToAmazonHandler}>
                                    <p className="text-xl text-context mr-2">Amazon Page</p>
                                    <FaExternalLinkAlt/>
                                </div>
                            </div>
                        </>
                    )
                }


            </Dialog.Body>
        </Dialog>
    )
}

export default ProductDialog
